.HifzSegments {
  position: relative; }
  .HifzSegments .HifzSegment {
    height: 100px;
    margin-bottom: 4px;
    pointer-events: none;
    position: absolute;
    width: 100%;
    box-sizing: border-box; }

.Page {
  height: 100%;
  position: relative; }
  .LeftPage .Page {
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#ffffff+0,f3e2c7+49,f3e2c7+78,b2a087+99 */
    background: #ffffff;
    /* Old browsers */
    background: -moz-linear-gradient(left, #ffffff 0%, #f3e2c7 49%, #f3e2c7 78%, #b2a087 99%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(left, #ffffff 0%, #f3e2c7 49%, #f3e2c7 78%, #b2a087 99%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to right, #ffffff 0%, #f3e2c7 49%, #f3e2c7 78%, #b2a087 99%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#b2a087',GradientType=1 );
    /* IE6-9 */ }
  .RightPage .Page {
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#f3e2c7+0,b2a087+6,ffffff+100 */
    background: #f3e2c7;
    /* Old browsers */
    background: -moz-linear-gradient(left, #f3e2c7 0%, #b2a087 6%, #ffffff 100%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(left, #f3e2c7 0%, #b2a087 6%, #ffffff 100%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to right, #f3e2c7 0%, #b2a087 6%, #ffffff 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f3e2c7', endColorstr='#ffffff',GradientType=1 );
    /* IE6-9 */ }
  .DarkTheme .Page {
    background: none;
    background-color: #333; }
  .Page .PageFrame {
    position: relative;
    height: calc(100% - 50px); }
    .Page .PageFrame .PageImageFrame {
      height: 100%;
      box-sizing: border-box;
      position: relative; }
      .Page .PageFrame .PageImageFrame .MaskContainer {
        position: absolute;
        pointer-events: none; }
        .Page .PageFrame .PageImageFrame .MaskContainer .Verse {
          position: absolute;
          background-color: #888; }
        .Page .PageFrame .PageImageFrame .MaskContainer .Mask {
          position: absolute;
          background-color: #999998fa; }
        .Page .PageFrame .PageImageFrame .MaskContainer button {
          transition: none; }
    .Page .PageFrame .VerseLayout {
      position: absolute;
      top: 0;
      height: 100%; }
      .Page .PageFrame .VerseLayout .Verse {
        box-sizing: border-box;
        position: absolute; }
        .Page .PageFrame .VerseLayout .Verse.Hovered {
          border-bottom: solid 2px silver; }
          .DarkTheme .Page .PageFrame .VerseLayout .Verse.Hovered {
            border-color: rgba(255, 255, 255, 0.2); }
        .Page .PageFrame .VerseLayout .Verse.Selected {
          background-color: #c8ccf0; }
          .DarkTheme .Page .PageFrame .VerseLayout .Verse.Selected {
            background-color: #265d8a; }
        .Page .PageFrame .VerseLayout .Verse.Playing {
          border-bottom: solid 2px green; }
  .Page .SelectionButton {
    color: black;
    background-color: #c8ccf0; }
    .DarkTheme .Page .SelectionButton {
      color: white;
      background-color: #265d8a; }
  .Page .PageImageFrame {
    position: relative;
    display: inline-block;
    height: 100%; }
  .Page .Spinner {
    position: absolute;
    left: calc(50% - 32px);
    top: calc(50% - 32px); }
  .Page img.PageImage {
    height: 100%;
    max-width: 100%;
    position: relative;
    pointer-events: none; }
    .DarkTheme .Page img.PageImage {
      filter: invert(1); }
  .Page .PageHeader {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 50px;
    min-width: 240px;
    color: #bbb;
    position: relative;
    background-color: #444;
    box-shadow: 0px 2px 3px 1px rgba(0, 0, 0, 0.35); }
    .Active .Page .PageHeader {
      background-color: black; }
    .narrow .Page .PageHeader {
      padding-left: 50px; }
    .Page .PageHeader .PageHeaderContent {
      position: relative;
      text-align: center; }
      [dir="rtl"] .Page .PageHeader .PageHeaderContent {
        direction: rtl; }
      .Page .PageHeader .PageHeaderContent > * {
        margin: 0 1px; }
      .Page .PageHeader .PageHeaderContent button {
        padding: 0 10px;
        line-height: 24px; }
      .Page .PageHeader .PageHeaderContent .PageHeaderSection {
        display: inline-block;
        white-space: nowrap;
        border-radius: 5px;
        height: 25px; }
        .Page .PageHeader .PageHeaderContent .PageHeaderSection > * {
          height: 100%; }
    .Page .PageHeader select {
      color: #aaa;
      height: 25px;
      background-color: transparent;
      border: none;
      border-radius: 5px;
      font-size: 14px;
      margin: 0 2px; }
      .Page .PageHeader select option {
        color: black;
        background-color: silver; }
    .Page .PageHeader .SuraTitle {
      right: 0; }
  .Page .PageFooter {
    position: absolute;
    width: 100%;
    top: 0;
    text-align: center; }

.LeftPage .AnimatePage {
  animation: LeftPageOpener 0.4s; }

.LeftPage .PageImageFrame {
  border-left: solid 1px silver; }

.RightPage .AnimatePage {
  animation: RightPageOpener 0.4s; }

.RightPage .PageImageFrame {
  border-right: solid 1px silver; }

@keyframes RightPageOpener {
  from {
    transform: scaleX(0.85);
    transform-origin: top left; }
  to {
    transform: scaleX(1);
    transform-origin: top left; } }

@keyframes LeftPageOpener {
  from {
    transform: scaleX(0.85);
    transform-origin: top right; }
  to {
    transform: scaleX(1);
    transform-origin: top right; } }
