#AudioPlayerPopup {
    .RecitersList {
        position: relative;
    }
    .ReciterButton {
        display: inline-block;
        position: absolute;
        width: 80px;
        min-height: 80px;
        text-align: center;
        padding: 5px;
        margin: 0;
        border-radius: 10px;
        transition: background-color 0.3s, left 0.4s, top 0.4s;
        &:hover {
            background-color: #444;
        }
        &.Selected {
            background-color: #666;
            cursor: default;
        }
        vertical-align: top;
        cursor: pointer;
        font-size: 12px;
    }
    .ReciterIcon {
        // float: right;
        width: auto;
        height: 50px;
    }
    .ModalContent {
        // top: auto;
        // bottom: 0;
        // background-color: #443322dd;
        // padding: 0 50px 0 0;
        // .Title {
        //     margin: 5px;
        // }
        // #PlayerControl {
        //     float: right;
        // }
        // #PlayerStatus {
        //     button {
        //         font-size: 15px;
        //         line-height: 40px;
        //         padding: 0 10px;
        //         box-sizing: content-box;
        //     }
        // }
        .ReciterIcon {
            width: 50px;
            height: 50px;
            border-radius: 10px;
        }
    }
}
