body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.SpreadSheet,
.FlowingList {
  list-style: none;
  margin: 0;
  padding-inline-start: 0;
  margin-block-start: 0;
  margin-block-end: 0; }
  .SpreadSheet li,
  .FlowingList li {
    list-style: none;
    display: inline-block;
    width: 100%;
    text-overflow: ellipsis;
    white-space: nowrap;
    position: relative; }
    .SpreadSheet li button,
    .FlowingList li button {
      width: 100%;
      padding: 8px;
      background-color: transparent;
      text-align: inherit; }
      .SpreadSheet li button:hover,
      .FlowingList li button:hover {
        background-color: #666; }
      .SpreadSheet li button.active,
      .FlowingList li button.active {
        background-color: #333; }
    .SpreadSheet li .actions,
    .FlowingList li .actions {
      position: absolute;
      top: 0;
      right: 0;
      z-index: 1; }
      [dir="rtl"] .SpreadSheet li .actions, [dir="rtl"]
      .FlowingList li .actions {
        left: 0;
        right: auto; }
      .SpreadSheet li .actions button,
      .FlowingList li .actions button {
        width: 30px;
        text-align: center;
        color: white; }

.FlowingList li {
  display: block;
  width: auto; }

.blinking {
  animation: smooth-blink 3s linear infinite; }

@keyframes blink {
  0% {
    opacity: 0; }
  50% {
    opacity: 0; }
  50.01% {
    opacity: 1; }
  100% {
    opacity: 1; } }

@keyframes smooth-blink {
  0% {
    opacity: 1; }
  50% {
    opacity: 0; } }

li.BookmarkRow > button {
  width: 100%;
  display: block;
  padding: 10px 100px 10px 10px; }
  [dir="rtl"] li.BookmarkRow > button {
    padding: 10px 10px 10px 100px; }
