.App {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  /* text-align: center; */
  background-color: #444;
  direction: rtl; }
  .App.DarkTheme {
    background-color: black; }

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #61dafb; }

button {
  transition: background-color 0.3s, top 0.5s;
  border: none;
  border-width: 0;
  background-color: transparent;
  color: #ddd;
  pointer-events: all;
  text-align: center; }
  button:hover {
    color: white !important;
    background: #666; }
  button * {
    pointer-events: none; }

.ButtonsBar {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  align-items: stretch;
  padding: 5px 4px;
  flex-grow: 1;
  min-height: 40px; }
  .ButtonsBar button {
    margin: 0 2px;
    padding: 0;
    border-radius: 6px;
    flex-grow: 1;
    font-size: 15px;
    background: #444; }
    .ButtonsBar button:hover {
      color: white;
      background: #666; }

.ActionsBar {
  display: flex;
  justify-content: flex-start;
  align-items: center; }
  .ActionsBar button {
    background-color: #444; }
    li .ActionsBar button {
      background-color: rgba(255, 255, 255, 0.2);
      width: inherit;
      margin: 0 2px; }

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }

hr {
  border-style: solid;
  border-width: 1px 0;
  border-color: #222 #888 #888 #222; }

.iBlock {
  display: inline-block;
  margin: 0 5px; }

.flipHorizontal {
  transform: scaleX(-1); }

.VerseInfo {
  float: left;
  display: flex;
  min-width: 60px;
  flex-direction: column; }
  .PopupBody .VerseInfo {
    margin: 0 10px 10px 0; }
  .VerseInfo .VerseInfoList {
    font-size: 13px;
    display: flex;
    flex-direction: column;
    align-items: center; }
  .VerseInfo button {
    min-height: 25px; }

.VerseText {
  margin: 0;
  line-height: 22px;
  color: #cfcf00;
  text-overflow: ellipsis;
  overflow: hidden; }

.circle-background,
.circle-progress {
  fill: none; }

.circle-background {
  stroke: #444; }

.circle-progress {
  stroke: yellowgreen;
  stroke-linecap: round;
  stroke-linejoin: round; }

.circle-text {
  font-size: 10px;
  font-weight: bold;
  fill: yellowgreen;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif; }

.CommandButton {
  font-size: 25px !important;
  height: 50px !important;
  min-width: 50px;
  vertical-align: top;
  padding: 0;
  pointer-events: fill; }
  .CommandButton img {
    vertical-align: middle; }

.GoodHifz {
  background-color: rgba(0, 128, 0, 0.15); }

.FairHifz {
  background-color: rgba(194, 149, 0, 0.15); }

.WeakHifz {
  background-color: rgba(177, 0, 0, 0.15); }

.HifzRangeRow {
  margin-bottom: 5px;
  border-radius: 5px; }
  .HifzRangeRow .AgeInfo {
    float: right; }
    [dir="rtl"] .HifzRangeRow .AgeInfo {
      float: left; }
  .HifzRangeRow .RangeInfo {
    font-weight: bold; }

.ContentFrame {
  padding: 15px 10px; }

.FootNote {
  border-top: solid 1px white;
  padding-top: 5px;
  margin-top: 15px;
  font-size: smaller;
  clear: both; }

.HiddenScroller {
  overflow: -moz-scrollbars-none;
  -ms-overflow-style: none; }
  .HiddenScroller::-webkit-scrollbar {
    width: 0 !important; }
