.ModalOverlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  z-index: 1;
  pointer-events: none; }
  .ModalOverlay.modal {
    pointer-events: all !important; }

.CancelButton {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 50px;
  height: 50px;
  color: white;
  font-size: 18px; }
  .CancelButton:hover {
    background-color: #666; }

.trans-start .ModalContent {
  opacity: 0;
  top: -20px; }

.ModalContent {
  pointer-events: all;
  position: absolute;
  right: 0;
  padding: 0 15px 0 15px;
  background-color: rgba(0, 0, 0, 0.9);
  color: white;
  box-sizing: border-box;
  max-height: 100%; }
  #ExercisePopup .ModalContent {
    background-color: rgba(0, 0, 0, 0.95); }
  .trans-end .ModalContent {
    transition: opacity 0.5s, top 0.5s, bottom 0.5s, left 0.2s, right 0.2s;
    opacity: 1;
    top: 0; }
    .trans-end .ModalContent.HiddenPopup {
      top: -20px;
      opacity: 0; }
  .ModalContent .Title {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    padding: 0 15px 0 50px;
    background-color: black;
    margin: 0 -15px 0 -15px;
    border-bottom: solid 1px #444;
    min-height: 50px;
    position: relative; }
    .ModalContent .Title button {
      font-size: 15px;
      min-width: 50px;
      height: 40px; }
    .ModalContent .Title .FlexTitle {
      display: inline-block;
      min-width: calc(100% - 115px); }
  .ModalContent .PopupBody {
    overflow: auto;
    margin: 15px 0; }
    .ModalContent .PopupBody .ButtonsBar {
      margin: 10px 0; }
  .ModalContent .OptionRow {
    padding: 5px 0;
    margin: 5px 0 10px; }
    .ModalContent .OptionRow label {
      display: block;
      width: 100%; }
      .ModalContent .OptionRow label > span {
        display: inline-block;
        margin: 0 12px;
        vertical-align: top;
        min-width: 100px;
        width: 36%;
        text-align: right; }
        [dir="rtl"] .ModalContent .OptionRow label > span {
          text-align: left; }
    .ModalContent .OptionRow input[type="radio"] {
      vertical-align: text-top; }
  .ModalContent .FieldRow {
    margin: 10px 0; }
    .ModalContent .FieldRow div {
      display: inline-block; }
  .ModalContent .FieldLabel {
    width: 38%;
    text-align: left;
    padding: 0 10px;
    box-sizing: border-box; }
  .ModalContent .FieldValue {
    width: 23%;
    text-align: right; }
    .ModalContent .FieldValue input {
      width: 100%;
      color: white;
      border: none;
      font-size: 25px;
      background-color: #ffffff30;
      vertical-align: middle; }
  .ModalContent .FieldAction {
    width: 23%;
    padding: 0 10px;
    box-sizing: border-box; }
  .ModalContent .ActionBar {
    margin: 20px 0 0 0;
    text-align: center; }
  .ModalContent .FieldAction button {
    background-color: #666;
    border: none;
    color: white;
    padding: 5px 15px;
    font-size: 17px;
    vertical-align: middle; }
    .ModalContent .FieldAction button:hover {
      background-color: black; }
  .ModalContent .CancelButton {
    font-size: 24px; }
  .ModalContent .CommandsList {
    text-align: center;
    direction: ltr;
    padding: 25px 0; }
    .ModalContent .CommandsList button {
      width: 80px;
      height: 80px !important;
      position: relative;
      font-size: 25px;
      border-radius: 6px;
      margin: 3px; }
      .ModalContent .CommandsList button .CommandLabel {
        font-size: 12px; }
      .ModalContent .CommandsList button .ReciterIcon {
        display: inline-block;
        border-radius: 6px;
        height: 34px;
        width: 34px;
        margin-bottom: -12px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover; }
  .ModalContent #SearchHistory {
    padding: 5px 0;
    white-space: nowrap;
    overflow-y: hidden;
    overflow-x: auto;
    height: 44px; }
    .ModalContent #SearchHistory button {
      font-size: 15px;
      padding: 3px 5px;
      border: solid 1px silver;
      margin: 0 3px; }
  .ModalContent .link {
    cursor: pointer;
    padding: 5px; }
    .ModalContent .link:hover {
      color: #ffffaa;
      background-color: #666; }
    .ModalContent .link * {
      pointer-events: none; }
  .ModalContent .ResultInfo {
    color: cyan;
    background-color: #444;
    border-radius: 5px;
    display: inline-block;
    min-width: 100px;
    text-align: left;
    padding: 0 5px; }
  .ModalContent .ResultsInfo {
    color: yellow;
    height: 35px;
    line-height: 30px; }
  .ModalContent .ResultsList {
    margin: 0;
    padding-inline-start: 30px; }
    .ModalContent .ResultsList .MoreResults {
      width: 100%;
      background-color: #888;
      padding: 10px;
      color: black;
      cursor: pointer; }
      .ModalContent .ResultsList .MoreResults:hover {
        background-color: #ddd; }
    .ModalContent .ResultsList .ResultItem button {
      text-align: right;
      font-size: 16px;
      line-height: 25px; }
      .ModalContent .ResultsList .ResultItem button span {
        pointer-events: none; }
      .ModalContent .ResultsList .ResultItem button .hiword {
        color: yellow; }
  .ModalContent a {
    color: white;
    text-decoration: none; }
  .ModalContent .TafseerVerse {
    color: yellow; }

#SearchPopup .Title .TypingConsole {
  margin: 0;
  flex-grow: 1;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding: 9px;
  box-sizing: border-box;
  line-height: 20px; }

#SearchPopup .Title .ButtonsBar {
  flex-grow: 0 !important;
  min-width: 50px; }

#SearchPopup .KeyboardFrame {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: black;
  padding: 10px 5px 0;
  pointer-events: fill; }

#IndexPopup .Title,
#FavoritesPopup .Title {
  justify-items: center;
  align-items: flex-end; }
  #IndexPopup .Title > span,
  #FavoritesPopup .Title > span {
