#IndexPopup #AKeyboard,
#FavoritesPopup #AKeyboard {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: black;
  z-index: 2; }

#ExercisePopup .PopupBody {
  margin: 0 -10px; }

#ExercisePopup #AKeyboard {
  margin: 0 0 5px 0; }

#ExercisePopup #VerseToTest {
  font-size: 25px; }

#ExercisePopup .Correct {
  color: greenyellow; }

#ExercisePopup .Wrong {
  color: #f33; }

.TypingConsole {
  padding: 10px;
  min-height: 21px;
  max-height: calc(100% - 40px);
  border: solid 1px #444;
  margin: 10px 0;
  font-size: 18px;
  overflow: auto;
  position: relative; }
  .TypingConsole.empty {
    color: #666; }
  .TypingConsole .TypingCursor {
    display: inline-block;
    width: 2px;
    background-color: #ffffff66;
    height: 20px;
    vertical-align: top; }
  .TypingConsole .ClearButton {
    position: absolute;
    right: 10px;
    top: 10px;
    color: #999; }
    [dir="rtl"] .TypingConsole .ClearButton {
      left: 10px;
      right: auto; }
    .TypingConsole .ClearButton:hover {
      color: white; }

#TrackDuration {
  display: flex;
  margin: 0 5px; }
